<template>
    <Doc>
        <H1>Code</H1>
        <P><Strong>Code</Strong></P>
        <Demo :demo="Code" title="">
            <div >Code</div>
        </Demo>

        <H2></H2>
        <P><Strong>onlyBtn</Strong>Code</P>
        <Demo :demo="CodeOnlyBtn" title="">
            <div ><Strong>onlyBtn</Strong></div>
        </Demo>

        <H2>Props</H2>
        <Props type="h3" :data="`{
            // 
            lang: {
                type: String,
                default: 'javascript'
            },
            // 
            onlyBtn: {
                type: Boolean,
                default: false
            },
            // 
            code: {
                type: String,
                required: true
            }
        }`"></Props>
    </Doc>
</template>
<script>
import Code from '../../demo/Advanced/Code/Code';
import CodeOnlyBtn from "../../demo/Advanced/Code/CodeOnlyBtn";
export default {
    data(){
        return {
            Code,
            CodeOnlyBtn,
        }
    }
}
</script>



