<template>
    <Doc>
        <H1>VueDemoDemo</H1>
        <P><Strong>Demo</Strong>Demo</P>
        <Demo :demo="Demo" title="VueDemo">
            <div >DemoVue</div>
        </Demo>

        <H2></H2>
        <P>Demo<Strong>/src/demo</Strong>Loader</P>
        <Demo :demo="DemoAutoCode" title="">
            <div >DemoVue</div>
        </Demo>

        <H2>Props</H2>
        <Props type="h3" :data="`{
            // 
            title: {
                type: String,
                default: ''
            },
            // 
            vertical: {
                type: Boolean,
                default: false
            },
            // VueComponentOptions
            demo: {
                type: Object,
                required: true,
            },
            // 
            code: {
                type: String,
                default: ''
            },
        }`"></Props>

        <H2>Slot</H2>
        <Instructions type="h3" :data="[{
            name: 'deflaut',
            describe: '',
        }]"></Instructions>
    </Doc>
</template>
<script>
import Demo from "../../demo/Advanced/Demo/Demo.vue";
import DemoAutoCode from "../../demo/Advanced/Demo/DemoAutoCode.vue";
export default {
    data(){
        return {
            Demo,
            DemoAutoCode,
        }
    }
}
</script>



